import { Form, Input } from 'antd';

const DescInput = () => {
  return (
    <Form.Item
      name="description"
      label="Descripcin:"
      rules={[
        {
          required: true,
          message: 'Por favor ingresa la descripcin'
        }
      ]}
      hasFeedback
    >
      <Input.TextArea rows={4} placeholder="Escriba la descripcin aqu" />
    </Form.Item>
  );
};

export default DescInput;