import { Form, Input } from 'antd';

const TitleInput = () => {
  return (
    <Form.Item
      name="title"
      label="Ttulo:"
      rules={[
        {
          required: true,
          message: 'Por favor ingresa el ttulo'
        }
      ]}
      hasFeedback
    >
      <Input placeholder="Escriba el ttulo aqu" />
    </Form.Item>
  );
}

export default TitleInput;